"use client";

import React from "react";
import { ArrowLeft } from "lucide-react";
import { useParams, useRouter } from "next/navigation";
import { formatDollar } from "@/shared/utils/currency.util";

interface OrderItem {
    id: number;
    status: string;
    statusColor: string;
    date: string;
    productName: string;
    quantity: number;
    price: number;
    image?: string;
}

const OrderDetailsPage = () => {
    const params = useParams();
    const router = useRouter();
    const orderId = params.id as string;

    const orderInfo = {
        orderId: `#${orderId}`,
        paymentMethod: "Paypal",
        subtotal: 650.5,
        shippingFee: 50.5,
        total: 700.5,
    };

    const billingAddress = {
        name: "Otor John Stephen",
        street: "38 Asheik Jarma Street, Jabi Abuja",
        email: "myemailaddress@gmail.com",
        phone: "08130000000",
    };

    const shippingAddress = {
        name: "Otor John Stephen",
        street: "38 Asheik Jarma Street, Jabi Abuja",
        email: "myemailaddress@gmail.com",
        phone: "08130000000",
    };

    const orderItems: OrderItem[] = [
        {
            id: 1,
            status: "Processing",
            statusColor: "bg-blue-500",
            date: "Tuesday 16th Dec, 2025",
            productName: "The name of the product",
            quantity: 2,
            price: 600.0,
        },
        {
            id: 2,
            status: "Shipped",
            statusColor: "bg-orange-500",
            date: "Tuesday 16th Dec, 2025",
            productName: "The name of the product",
            quantity: 2,
            price: 600.0,
        },
    ];

    return (
        <div className="bg-white rounded-[20px] p-5 border border-gray-200">
            {/* Header */}
            <div className="mb-6">
                <button
                    onClick={() => router.back()}
                    className="flex items-center gap-2 text-gray-700 hover:text-gray-900 mb-2 transition-colors cursor-pointer"
                >
                    <ArrowLeft className="w-5 h-5" />
                    <span className="text-xl font-semibold">Order Details</span>
                </button>
                <p className="text-sm text-gray-600">
                    View the details of your order and track order
                </p>
            </div>

            {/* Address Cards and Order Summary */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
                {/* Billing Address */}
                <div className="bg-white border border-gray-200 rounded-xl p-5">
                    <h3 className="font-semibold text-gray-900 mb-4">Billing Address</h3>
                    <div className="space-y-3 text-sm">
                        <div>
                            <p className="font-medium text-gray-900">{billingAddress.name}</p>
                            <p className="text-gray-600">{billingAddress.street}</p>
                        </div>
                        <div>
                            <p className="text-gray-500 text-xs mb-1">Email</p>
                            <p className="text-gray-900">{billingAddress.email}</p>
                        </div>
                        <div>
                            <p className="text-gray-500 text-xs mb-1">Phone</p>
                            <p className="text-gray-900">{billingAddress.phone}</p>
                        </div>
                    </div>
                </div>

                {/* Shipping Address */}
                <div className="bg-white border border-gray-200 rounded-xl p-5">
                    <h3 className="font-semibold text-gray-900 mb-4">Shipping Address</h3>
                    <div className="space-y-3 text-sm">
                        <div>
                            <p className="font-medium text-gray-900">{shippingAddress.name}</p>
                            <p className="text-gray-600">{shippingAddress.street}</p>
                        </div>
                        <div>
                            <p className="text-gray-500 text-xs mb-1">Email</p>
                            <p className="text-gray-900">{shippingAddress.email}</p>
                        </div>
                        <div>
                            <p className="text-gray-500 text-xs mb-1">Phone</p>
                            <p className="text-gray-900">{shippingAddress.phone}</p>
                        </div>
                    </div>
                </div>

                {/* Order Summary */}
                <div className="bg-white border border-gray-200 rounded-xl p-5">
                    <div className="space-y-3 text-sm">
                        <div className="flex justify-between">
                            <span className="text-gray-600">Order ID</span>
                            <span className="font-medium text-gray-900">{orderInfo.orderId}</span>
                        </div>
                        <div className="flex justify-between">
                            <span className="text-gray-600">Payment method</span>
                            <span className="font-medium text-gray-900">
                                {orderInfo.paymentMethod}
                            </span>
                        </div>
                        <div className="flex justify-between pt-3 border-t border-gray-200">
                            <span className="text-gray-600">Subtotal:</span>
                            <span className="font-medium text-gray-900">
                                {formatDollar(orderInfo.subtotal)}
                            </span>
                        </div>
                        <div className="flex justify-between">
                            <span className="text-gray-600">Shipping fee:</span>
                            <span className="font-medium text-gray-900">
                                {formatDollar(orderInfo.shippingFee)}
                            </span>
                        </div>
                        <div className="flex justify-between pt-3 border-t border-gray-200">
                            <span className="text-gray-900 font-semibold">Total:</span>
                            <span className="font-bold text-orange-500 text-lg">
                                {formatDollar(orderInfo.total)}
                            </span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Items in Your Order */}
            <div className="bg-white border border-gray-200 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-6">
                    Items in your order
                </h3>

                <div className="space-y-6">
                    {orderItems.map((item) => (
                        <div key={item.id}>
                            {/* Status Badge */}
                            <div className="flex items-center gap-3 mb-3">
                                <span
                                    className={`${item.statusColor} text-white text-xs font-medium px-4 py-1.5 rounded-full`}
                                >
                                    Status: {item.status}
                                </span>
                            </div>

                            {/* Date */}
                            <p className="text-sm text-gray-600 mb-4">On {item.date}</p>

                            {/* Product Details */}
                            <div className="flex items-center justify-between pb-6 border-b border-gray-200 last:border-b-0 last:pb-0">
                                <div className="flex items-center gap-4">
                                    <div className="w-20 h-20 bg-gray-200 rounded-lg flex-shrink-0"></div>
                                    <div>
                                        <p className="font-medium text-gray-900 mb-1">
                                            {item.productName}
                                        </p>
                                        <p className="text-sm text-gray-600 mb-1">
                                            QTY: {item.quantity}
                                        </p>
                                        <p className="text-base font-semibold text-gray-900">
                                            {formatDollar(item.price)}
                                        </p>
                                    </div>
                                </div>

                                {/* Action Buttons */}
                                <div className="flex gap-3">
                                    <button className="bg-emerald-900 hover:bg-emerald-950 text-white px-6 py-2.5 rounded-lg text-sm font-medium transition-colors">
                                        Cancel Order
                                    </button>
                                    <button className="bg-orange-500 hover:bg-orange-600 text-white px-6 py-2.5 rounded-lg text-sm font-medium transition-colors">
                                        See Status History
                                    </button>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default OrderDetailsPage;